var createRoom = function createRoom(room) {
  return {
    type: 'socket',
    types: [CREATE_ROOM, CREATE_ROOM_SUCCESS, CREATE_ROOM_FAIL],
    promise: function promise(socket) {
      return socket.emit('create room', room);
    }
  };
};

var deleteAudioBlob = function deleteAudioBlob(blob) {
  return {
    type: DELETE_AUDIO_BLOB,
    blob: blob
  };
};

var getRooms = function getRooms() {
  return {
    type: 'socket',
    types: [GET_ROOMS, GET_ROOMS_SUCCESS, GET_ROOMS_FAIL],
    promise: function promise(socket) {
      return socket.emit('get rooms');
    }
  };
};

var joinRoom = function joinRoom(room) {
  return {
    type: 'socket',
    types: [JOIN_ROOM, JOIN_ROOM_SUCCESS, JOIN_ROOM_FAIL],
    promise: function promise(socket) {
      return socket.emit('join room', room);
    }
  };
};

var loadAudioBlob = function loadAudioBlob(blob) {
  return {
    type: LOAD_AUDIO_BLOB,
    blob: blob
  };
};

//TODO: remove this action?
var loadSocketNsps = function loadSocketNsps(socket) {
  return {
    type: 'socket',
    types: [LOAD_SOCKET_NSPS, LOAD_SOCKET_NSPS_SUCCESS, LOAD_SOCKET_NSPS_FAIL],
    promise: function promise(socket) {
      return socket.connect();
    }
  };
};

var nspConnect = function nspConnect(namespace) {
  return {
    type: 'socket',
    types: [GET_ROOMS, GET_ROOMS_SUCCESS, GET_ROOMS_FAIL],
    promise: function promise(socket) {
      return socket.nspConnect(namespace);
    }
  };
};

var receiveAudioBlob = function receiveAudioBlob(data) {
  return function (dispatch) {
    dispatch({
      type: 'socket',
      types: [RECEIVE_AUDIO_BLOB, RECEIVE_AUDIO_BLOB_SUCCESS, RECEIVE_AUDIO_BLOB_FAIL],
      promise: function promise(socket) {
        return socket.on('receive audio blob', data).then(function (result) {
          dispatch(addAudio({
            author: result.audio.author,
            dataUrl: result.audio.dataUrl
          }));
        });
      }
    });
  };
};

var receiveMsg = function receiveMsg(body) {
  return function (dispatch) {
    dispatch({
      type: 'socket',
      types: [RECEIVE_MSG, RECEIVE_MSG_SUCCESS, RECEIVE_MSG_FAIL],
      promise: function promise(socket) {
        return socket.on('receive  msg', body).then(function (result) {
          dispatch(addMsg({ author: result.author, msg: result.msg }));
        });
      }
    });
  };
};

var receiveRoomMeta = function receiveRoomMeta(meta) {
  return function (dispatch) {
    dispatch({
      type: 'socket',
      types: [RECEIVE_ROOM_META, RECEIVE_ROOM_META_SUCCESS, RECEIVE_ROOM_META_FAIL],
      promise: function promise(socket) {
        return socket.on('receive room meta', meta).then(function (result) {
          dispatch(loadQuestion(result.question));
          dispatch(updatePicture(result.src));
          dispatch(loadListType(result.listType));
          dispatch(updateTranslation(result.translation));
        });
      }
    });
  };
};

var sendAudioBlob = function sendAudioBlob(files) {
  return {
    type: 'socket',
    types: [SEND_AUDIO_BLOB, SEND_AUDIO_BLOB_SUCCESS, SEND_AUDIO_BLOB_FAIL],
    promise: function promise(socket) {
      return socket.emit('send audio blob', files);
    }
  };
};

var sendMsg = function sendMsg(msg) {
  return {
    type: 'socket',
    types: [SEND_MSG, SEND_MSG_SUCCESS, SEND_MSG_FAIL],
    promise: function promise(socket) {
      return socket.emit('send msg', msg);
    }
  };
};

var sendRoomMeta = function sendRoomMeta(meta) {
  return {
    type: 'socket',
    types: [SEND_ROOM_META, SEND_ROOM_META_SUCCESS, SEND_ROOM_META_FAIL],
    promise: function promise(socket) {
      return socket.emit('send room meta', meta);
    }
  };
};

