import { CONTEXT_MENU_HIDE, CONTEXT_MENU_SHOW } from './types.js';

export function showContextMenu(x, y, type, menuArgs) {
  return {
    type: CONTEXT_MENU_SHOW,
    payload: {
      location: {
        x: x,
        y: y
      },
      type: type,
      menuArgs: menuArgs
    }
  };
}

export function hideContextMenu() {
  return {
    type: CONTEXT_MENU_HIDE
  };
}
